.label {
  font-family: 'Yandex Sans', sans serif;
}

.slice {
  box-shadow: rgba(149, 157, 165, 0.2) 0 8px 24px;
}

.sliceAppear {
  transform: rotate(var(--start-angle-start-value));
  transform-origin: center;
  stroke-dasharray: var(--slice-length-start-value) var(--slice-length-end-value);
  transition: all calc(var(--animation-duration) * 1s) calc(var(--animation-delay) * 1s) var(--timing-function);
}

.sliceAppearActive {
  transform: rotate(0);
  stroke-dasharray: var(--slice-length-end-value) var(--slice-length-end-value);
  transform-origin: center;
}
